<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="yuyuebianhao">
				<el-input v-model="ruleForm.yuyuebianhao" 
					placeholder="" clearable :disabled=" false  ||ro.yuyuebianhao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="kechengmingcheng">
				<el-input v-model="ruleForm.kechengmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.kechengmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajiaozhanghao">
				<el-input v-model="ruleForm.jiajiaozhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.jiajiaozhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajiaoxingming">
				<el-input v-model="ruleForm.jiajiaoxingming" 
					placeholder="" clearable :disabled=" false  ||ro.jiajiaoxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tousushijian">
				<el-date-picker
					:disabled=" false  ||ro.tousushijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.tousushijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xuehao">
				<el-select  @change="xuehaoChange" v-model="ruleForm.xuehao" placeholder="">
					<el-option
						v-for="(item,index) in xuehaoOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xueshengxingming">
				<el-input v-model="ruleForm.xueshengxingming" 
					placeholder="" clearable :disabled=" false  ||ro.xueshengxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiazhangzhanghao">
				<el-input v-model="ruleForm.jiazhangzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.jiazhangzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiazhangxingming">
				<el-input v-model="ruleForm.jiazhangxingming" 
					placeholder="" clearable :disabled=" false  ||ro.jiazhangxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="lianxidianhua">
				<el-input v-model="ruleForm.lianxidianhua" 
					placeholder="" clearable :disabled=" false  ||ro.lianxidianhua"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tousuneirong">
				<editor 
					v-model="ruleForm.tousuneirong" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					yuyuebianhao : false,
					kechengmingcheng : false,
					fengmian : false,
					jiajiaozhanghao : false,
					jiajiaoxingming : false,
					tousuneirong : false,
					tousushijian : false,
					xuehao : false,
					xueshengxingming : false,
					jiazhangzhanghao : false,
					jiazhangxingming : false,
					lianxidianhua : false,
					sfsh : false,
					shhf : false,
					userid : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					yuyuebianhao: '',
					kechengmingcheng: '',
					fengmian: '',
					jiajiaozhanghao: '',
					jiajiaoxingming: '',
					tousuneirong: '',
					tousushijian: '',
					xuehao: '',
					xueshengxingming: '',
					jiazhangzhanghao: '',
					jiazhangxingming: '',
					lianxidianhua: '',
					userid: '',
				},
				xuehaoOptions: [],


				rules: {
					yuyuebianhao: [
					],
					kechengmingcheng: [
					],
					fengmian: [
					],
					jiajiaozhanghao: [
					],
					jiajiaoxingming: [
					],
					tousuneirong: [
						{ required: true, message: '', trigger: 'blur' },
					],
					tousushijian: [
					],
					xuehao: [
					],
					xueshengxingming: [
					],
					jiazhangzhanghao: [
					],
					jiazhangxingming: [
					],
					lianxidianhua: [
					],
					sfsh: [
					],
					shhf: [
					],
					userid: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.tousushijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='yuyuebianhao'){
							this.ruleForm.yuyuebianhao = obj[o];
							this.ro.yuyuebianhao = true;
							continue;
						}
						if(o=='kechengmingcheng'){
							this.ruleForm.kechengmingcheng = obj[o];
							this.ro.kechengmingcheng = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='jiajiaozhanghao'){
							this.ruleForm.jiajiaozhanghao = obj[o];
							this.ro.jiajiaozhanghao = true;
							continue;
						}
						if(o=='jiajiaoxingming'){
							this.ruleForm.jiajiaoxingming = obj[o];
							this.ro.jiajiaoxingming = true;
							continue;
						}
						if(o=='tousuneirong'){
							this.ruleForm.tousuneirong = obj[o];
							this.ro.tousuneirong = true;
							continue;
						}
						if(o=='tousushijian'){
							this.ruleForm.tousushijian = obj[o];
							this.ro.tousushijian = true;
							continue;
						}
						if(o=='xuehao'){
							this.ruleForm.xuehao = obj[o];
							this.ro.xuehao = true;
							continue;
						}
						if(o=='xueshengxingming'){
							this.ruleForm.xueshengxingming = obj[o];
							this.ro.xueshengxingming = true;
							continue;
						}
						if(o=='jiazhangzhanghao'){
							this.ruleForm.jiazhangzhanghao = obj[o];
							this.ro.jiazhangzhanghao = true;
							continue;
						}
						if(o=='jiazhangxingming'){
							this.ruleForm.jiazhangxingming = obj[o];
							this.ro.jiazhangxingming = true;
							continue;
						}
						if(o=='lianxidianhua'){
							this.ruleForm.lianxidianhua = obj[o];
							this.ro.lianxidianhua = true;
							continue;
						}
						if(o=='userid'){
							this.ruleForm.userid = obj[o];
							this.ro.userid = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.jiazhangzhanghao!=''&&json.jiazhangzhanghao) || json.jiazhangzhanghao==0){
							this.ruleForm.jiazhangzhanghao = json.jiazhangzhanghao;
							this.ro.jiazhangzhanghao = true;
						}
						if((json.jiazhangxingming!=''&&json.jiazhangxingming) || json.jiazhangxingming==0){
							this.ruleForm.jiazhangxingming = json.jiazhangxingming;
							this.ro.jiazhangxingming = true;
						}
						if((json.lianxidianhua!=''&&json.lianxidianhua) || json.lianxidianhua==0){
							this.ruleForm.lianxidianhua = json.lianxidianhua;
							this.ro.lianxidianhua = true;
						}
					}
				});
				this.$http.get('option/xuesheng/xuehao', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.xuehaoOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},
			// 
			xuehaoChange () {
				this.$http.get('follow/xuesheng/xuehao?columnValue=' + this.ruleForm.xuehao, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						if(res.data.data.xueshengxingming){
							this.ruleForm.xueshengxingming = res.data.data.xueshengxingming
						}
					}
				});
			},

			// 
			// 
			info() {
				this.$http.get(`tousujianyi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`tousujianyi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
