<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="kechengmingcheng">
				<el-input v-model="ruleForm.kechengmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.kechengmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="xuekefenlei">
				<el-select v-model="ruleForm.xuekefenlei" placeholder="" :disabled=" false  ||ro.xuekefenlei" >
					<el-option
						v-for="(item,index) in xuekefenleiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiaoshounianji">
				<el-input v-model="ruleForm.jiaoshounianji" 
					placeholder="" clearable :disabled=" false  ||ro.jiaoshounianji"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xiaoshijiage">
				<el-input v-model.number="ruleForm.xiaoshijiage" 
					placeholder="" clearable :disabled=" false  ||ro.xiaoshijiage"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="keyueshijian">
				<el-input v-model="ruleForm.keyueshijian" 
					placeholder="" clearable :disabled=" false  ||ro.keyueshijian"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajiaozhanghao">
				<el-select  @change="jiajiaozhanghaoChange" v-model="ruleForm.jiajiaozhanghao" placeholder="">
					<el-option
						v-for="(item,index) in jiajiaozhanghaoOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajiaoxingming">
				<el-input v-model="ruleForm.jiajiaoxingming" 
					placeholder="" clearable :disabled=" false  ||ro.jiajiaoxingming"></el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					kechengmingcheng : false,
					fengmian : false,
					xuekefenlei : false,
					jiaoshounianji : false,
					xiaoshijiage : false,
					keyueshijian : false,
					jiajiaozhanghao : false,
					jiajiaoxingming : false,
					clicktime : false,
					discussnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					kechengmingcheng: '',
					fengmian: '',
					xuekefenlei: '',
					jiaoshounianji: '',
					xiaoshijiage: '',
					keyueshijian: '',
					jiajiaozhanghao: '',
					jiajiaoxingming: '',
					clicktime: '',
					discussnum: '',
				},
				xuekefenleiOptions: [],
				jiajiaozhanghaoOptions: [],


				rules: {
					kechengmingcheng: [
						{ required: true, message: '', trigger: 'blur' },
					],
					fengmian: [
					],
					xuekefenlei: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jiaoshounianji: [
					],
					xiaoshijiage: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					keyueshijian: [
					],
					jiajiaozhanghao: [
					],
					jiajiaoxingming: [
					],
					clicktime: [
					],
					discussnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='kechengmingcheng'){
							this.ruleForm.kechengmingcheng = obj[o];
							this.ro.kechengmingcheng = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='xuekefenlei'){
							this.ruleForm.xuekefenlei = obj[o];
							this.ro.xuekefenlei = true;
							continue;
						}
						if(o=='jiaoshounianji'){
							this.ruleForm.jiaoshounianji = obj[o];
							this.ro.jiaoshounianji = true;
							continue;
						}
						if(o=='xiaoshijiage'){
							this.ruleForm.xiaoshijiage = obj[o];
							this.ro.xiaoshijiage = true;
							continue;
						}
						if(o=='keyueshijian'){
							this.ruleForm.keyueshijian = obj[o];
							this.ro.keyueshijian = true;
							continue;
						}
						if(o=='jiajiaozhanghao'){
							this.ruleForm.jiajiaozhanghao = obj[o];
							this.ro.jiajiaozhanghao = true;
							continue;
						}
						if(o=='jiajiaoxingming'){
							this.ruleForm.jiajiaoxingming = obj[o];
							this.ro.jiajiaoxingming = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.$http.get('option/xuekefenlei/xuekefenlei', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.xuekefenleiOptions = res.data.data;
					}
				});
				this.$http.get('option/jiajiao/jiajiaozhanghao', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.jiajiaozhanghaoOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},
			// 
			jiajiaozhanghaoChange () {
				this.$http.get('follow/jiajiao/jiajiaozhanghao?columnValue=' + this.ruleForm.jiajiaozhanghao, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						if(res.data.data.jiajiaoxingming){
							this.ruleForm.jiajiaoxingming = res.data.data.jiajiaoxingming
						}
					}
				});
			},

			// 
			// 
			info() {
				this.$http.get(`jiajiaoxinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`jiajiaoxinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

